// ==========================================================================
// Objects / Buttons
// ==========================================================================

//
// 1. Allow us to style box model properties.
// 2. Fixes odd inner spacing in IE7.
// 3. Line different sized buttons up a little nicer.
// 4. Force all button-styled elements to appear clickable.
//
button,
.o-button
{
    // Base
    color: $primary;
    letter-spacing: 0.25px;
    font-weight: $medium;
    font-size: 14px;
    text-decoration: none;
    text-transform: uppercase;
    white-space: nowrap;
    text-align: center;
    line-height: 36px;
    
    padding: 0 8px;
    min-width: 64px;
    height: 36px;
    margin: 0 8px;
    
    display: inline-block;
    user-select: none;
    border-radius: 2px;
    background: rgba(0,0,0,0);
    transition: background 125ms $ease-out;

    @include u-hocus{
        background: rgba($primary, 0.08);
        transition: background 150ms $ease-in;
    }

    &:active{
        background: rgba($primary, 0.15);
        transition: background 150ms $ease-sharp;
    }

    // Modifier Classes
    &.is-disabled{
        color: rgba(0,0,0,0.26);
        pointer-events: none;

        section.-dark-background & {
            color: rgba($white, 0.3);
        }
    }

    &.-wide{
        display: block;
    }

    &.has-icon{
        padding: 0 16px 0 12px;

        svg{
            @include size(18px, 18px);
            transform: translateY(3px);
            margin-right: 8px;
        }
    }

    // Solid
    &.-solid{
        padding: 0 16px;
        background: $primary;
        color: $white;

        &:active{
            background: darken($primary, 3);
        }

        &.is-disabled{
            background: rgba($black,0.1);
            color: rgba($black,0.26);

            section.-dark-background & {
                background: rgba($white,0.05);
                color: rgba($white, 0.3);
            }
        }
    }

    // Outline
    &.-outline{
        padding: 0 16px;
        box-shadow: 0 0 0 2px rgba($black,0.05);

        section.-dark-background & {
            box-shadow: 0 0 0 2px rgba($white,0.05);
        }
    }

    // Icon ONLY
    &.-icon{
        background: rgba(0,0,0,0);
        color: $primary;

        html.has-js & {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            padding: 0;

            .-active{
                display: none;
            }

            &.is-active{
                .-inactive{
                    display: none;
                }
                
                .-active{
                    display: initial;
                }
            }

            &::before{
                content:"";
                background: $primary;
                position: absolute;
                display: inline-block;
                border-radius: 100%;
                @include size(26px, 26px);
                transform: scale(0);
                opacity: 0;
                transition: transform 125ms $ease-sharp, opacity 25ms 100ms $ease-out;
            }

            &::after{
                content:"";
                background: $primary;
                position: absolute;
                display: inline-block;
                border-radius: 100%;
                @include size(28px, 28px);
                transform: scale(0);
                opacity: 0;
                transition: transform 75ms $ease-sharp, opacity 25ms 50ms $ease-out;
            }

            svg{
                @include size(18px, 18px);
            }

            .-alt{
                display: none !important;
            }

            &:hover{
                &:before{
                    transform: scale(1);
                    opacity: 0.15;
                    transition: transform 150ms $ease-in, opacity 75ms $ease-in;
                }
            }

            &:active{
                &:before{
                    opacity: 0;
                    transition: opacity 75ms $ease-out;
                }

                &:after{
                    transform: scale(1);
                    opacity: 0.25;
                    transition: transform 150ms $ease-in, opacity 75ms $ease-in;
                }
            }
        }
    }
}

@keyframes iconPop{
    0%{
        transform: scale(0);
        opacity: 0;
    }
    75%{
        transform: scale(1);
        opacity: 1;
        animation-timing-function: $ease-in;
    }
    100%{
        transform: scale(1);
        opacity: 0;
        animation-timing-function: $ease-sharp;
    }
}